
import { defineComponent, PropType } from "vue";

export default defineComponent({
  props: {
    title: {
      type: String,
      default: "",
    },
    listData: {
      //不能叫userList
      type: Array, //不好判断数组里有什么，不用as。。
      required: true,
    },
    listCount: {
      type: Number,
      default: 0,
    },
    propList: {
      type: Array as PropType<any[]>, //自己写的，解决了很牛。学的form.vue写的断言，propType。看了propList发现不能用object，换成了any
      // required: true,//把这里注释掉，page-content.vue的hy-table就不会报错了，不懂
    },
    showIndexColumn: {
      type: Boolean,
      default: false, //默认不显示
    },
    showSelectColumn: {
      type: Boolean,
      default: false,
    },
    page: {
      type: Object,
      default: () => ({ currentPage: 0, pageSize: 10 }), //默认0,10
    },
    childrenProps: {
      type: Object,
      default: () => ({}),
    },
    showFooter: {
      type: Boolean,
      default: true,
    },
  },
  emits: ["selectionChange", "update:page"], //要拿到数据的是页面，所以要把数据发出去emit
  setup(props, { emit }) {
    const handleSelectionChange = (value: any) => {
      // console.log(value)//可以先用log测试一下有没有取到值，写emit是为了把数据给页面
      emit("selectionChange", value);
    };
    const handleCurrentChange = (currentPage: number) => {
      emit("update:page", { ...props.page, currentPage });
    };

    const handleSizeChange = (pageSize: number) => {
      emit("update:page", { ...props.page, pageSize });
    };

    return {
      handleSelectionChange,
      handleCurrentChange,
      handleSizeChange,
    };
  },
});
